@mixin wrap {
  @media (min-width: $tablet-width) {
    position: relative;
    margin-left: auto;
    margin-right: auto;
    width: $tablet-width;
    padding-right: 30px;
    padding-left: 30px;
  }

  @media (min-width: $desktop-width) {
    width: $desktop-width;
    padding-right: 40px;
    padding-left: 40px;
  }
}
